import xlistInput from '../../wxcomponents/xlist/xlist-input.vue'
import xlistPicker from '../../wxcomponents/xlist/xlist-picker.vue'

import amap from '../../common/amap-wx.js'
import getcode from '../../wxcomponents/getcode/getcode.vue'
import xlocation from '../../wxcomponents/xlocation/xlocation.vue'
const formatDate = require('../../util/util.js')
export default {
	onLoad() {
		this.selectedIndex()
		this.selectedIndexType()
	},
	data() {
		return {
			scode:'',
			phoneCode:'',
			selectedItemTypeIndex:'',
			selectedItemType:'',
			selectedItem:'',
			province:'',
			city:'',
			district:'',
			township:'',
			title: '验证码：',
			name: '',
			manager: '',
			phone: '',
			getCode:'',
			area: '',
			street: '',
			detail_address: '',
			businessType: [
				'个人', '公司'
			],
			repairType: [
				'上门维修', '到店维修'
			],
			index: 0
		}
	},
	methods: {
		bindScode(e){
			this.scode = e.scode
		},
		bindPhoneCode(e){
			this.phoneCode = e.phoneCode
		},
		bindInputName(e){
			this.name = e
			uni.setStorage({
				key:'partyName',
				data: e
			})
		},
		bindInputManager(e){
			this.manager = e
			uni.setStorage({
				key:'partyManager',
				data: e
			})
		},
		bindInputPhone(e){
			this.phone = e
			uni.setStorage({
				key:'partyPhone',
				data: e
			})
		},
		onSave() {
			let scode = this.scode.toString()
			let phoneCode = this.phoneCode.toString()
			if(this.name == ''){
				uni.showToast({
					title: '请填写商家名称',
					icon:'none'
				})
			}else if (this.manager == '') {
				uni.showToast({
					title:'请填写负责人',
					icon:'none'
				})
			}else if (this.phone == ''){
				uni.showToast({
					title: '请填写手机号码',
					icon:'none'
				})
			}else if(this.detail_address == ''){
				uni.showToast({
					title: '请填写详细地址',
					icon:'none'
				})
			}else if(this.scode == ''){
				uni.showToast({
					title: '请填写验证码',
					icon:'none'
				});
			}else if( scode !== phoneCode){
				uni.showToast({
					title: '请输入正确的验证码',
					icon:'none'
				})
			}else {
				this.onPost()
				uni.showToast({
					title: '保存成功'
				})
				setTimeout(() => {
					uni.hideToast()
					uni.navigateTo({
						url: 'upload-photo'
					})
				}, 2000)
			}
		},
		selectedIndex(e) {
			this.selectedItem = e ? this.businessType[e] :this.businessType[0]
		},
		selectedIndexType(e){
			this.selectedItemTypeIndex = e
			this.selectedItemType = e ? this.repairType[e] :this.repairType[0]
		},
		onArea(e) {
			this.area = e.district
			this.street = e.township
		},
		onDetail(e){
			this.detail_address = e.detailAddress
			uni.setStorage({
				key:'partyAddress',
				data:this.area + this.street + this.detail_address
			})
		},
		bindCode(e){
			console.log(e.codeText)
			this.getCode = e.codeText
		},
		onPost() {
			uni.request({
				url:'https://120.24.180.246/xmRepair/shopInfo/add',
				method:'POST',
				data:{
					name: this.name,
					manager:this.manager,
					phone:this.phone,
					area:this.area,
					street:this.street,
					detail_address:this.detail_address,
					type:this.selectedItem,
					service_mode:this.selectedItemTypeIndex,
					stat:0
				},
				success: res=>{
					console.log('success')
				}
			})
		}
	},
	components: {
		getcode,
		xlocation,
		xlistInput,
		xlistPicker
	}
}
